input {
  margin: 0;
  padding: 0;
  border: 0;
  font-weight: normal;
  vertical-align: baseline;
}
a {
  color: #8A8A8A;
  text-decoration: none;
  -webkit-backface-visibility: hidden;
}
* {
  box-sizing: border-box;
}
li {
  list-style: none;
}
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
::-webkit-scrollbar-track-piece {
  background-color: #FFF;
  -webkit-border-radius: 6px;
  opacity: 0;
}
::-webkit-scrollbar-thumb:vertical {
  height: 5px;
  background-color: #ccc;
  -webkit-border-radius: 6px;
}
::-webkit-scrollbar-thumb:horizontal {
  width: 5px;
  background-color: #ccc;
  -webkit-border-radius: 6px;
}
input:focus,
button:focus,
textarea:focus {
  outline: none;
}
body,
input,
textarea {
  font-family: "THEX", '微软雅黑', Arial;
  font-size: 12px;
}
.login-page {
  width: 350px;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 10px;
  transform: translate(-50%, -50%);
  padding: 30px;
  box-sizing: border-box;
}
.login-logo {
  text-align: center;
  margin-bottom: 30px;
}
.login-logo img {
  width: 90px;
}
.chart-component {
  position: absolute;
}
.chart-component .chart-component {
  pointer-events: none;
}
.chart-component .drag {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
}
.chart-component .drag.hover {
  background-color: rgba(255, 0, 0, 0.1);
  border: 1px solid #F00;
}
.chart-component .drag.isEdit {
  background-color: rgba(255, 0, 0, 0.1);
  border: 1px solid #F00;
}
.chart-component .drag.isEdit .grid-line,
.chart-component .drag.isEdit .resize-handle-wrapper {
  display: block;
}
.chart-component .drag .move-tap {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  cursor: move;
}
.chart-component .drag .resize-handle-wrapper {
  display: none;
}
.chart-component .drag .resize-handle-wrapper span {
  position: absolute;
  box-sizing: border-box;
  border: 3px solid #F00;
  width: 8px;
  height: 8px;
  display: block;
}
.chart-component .drag .resize-handle-wrapper span.move-width {
  top: 50%;
  right: -4px;
  transform: translateY(-50%);
  cursor: ew-resize;
  border-left: 0;
  border-top: 0;
  border-bottom: 0;
}
.chart-component .drag .resize-handle-wrapper span.move-height {
  bottom: -4px;
  left: 50%;
  transform: translateX(-50%);
  cursor: ns-resize;
  border-left: 0;
  border-right: 0;
  border-top: 0;
}
.chart-component .drag .resize-handle-wrapper span.move-resize {
  bottom: -8px;
  right: -4px;
  transform: translateY(-50%);
  cursor: nwse-resize;
  border-left: 0;
  border-top: 0;
}
.chart-component .drag .grid-line {
  display: none;
}
.chart-component .drag .grid-line .grid-line-top {
  position: absolute;
  border-left: 1px dashed #F00;
  width: 0;
  height: 10000px;
  left: 0;
  transform: translateY(-100%);
}
.chart-component .drag .grid-line .grid-line-left {
  position: absolute;
  border-top: 1px dashed #F00;
  width: 10000px;
  height: 0;
  top: 0;
  transform: translateX(-100%);
}
.chart-component .drag .grid-line .grid-line-label {
  position: absolute;
  padding: 5px;
  transform: translate(-100%, -100%);
  color: #F00;
  white-space: nowrap;
}
.chart-rect {
  position: absolute;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 10px;
  background: blue;
  border-radius: 5px;
  color: #FFF;
  font-size: 20px;
  text-align: center;
}
.chart-icon {
  position: absolute;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.chart-icon .icons {
  width: 40px;
  height: 40px;
  background: yellow;
}
.editor-container {
  width: 100%;
  height: 100%;
  position: relative;
}
.editor-container .editor-toolbar__top {
  position: absolute;
  height: 60px;
  display: flex;
  background: #303030;
  width: 100%;
  align-items: center;
  z-index: 3;
}
.editor-container .editor-toolbar__top .navbar-header {
  width: 100px;
  text-align: center;
}
.editor-container .editor-toolbar__top .navbar-header img {
  width: 45px;
}
.editor-container .editor-toolbar__top .editor-toolbar {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.editor-container .editor-toolbar__top .editor-toolbar .editor-toolbar__left {
  flex: 1;
}
.editor-container .editor-toolbar__top .editor-toolbar .editor-toolbar__center {
  flex: 2;
  display: flex;
  align-items: center;
  justify-content: center;
}
.editor-container .editor-toolbar__top .editor-toolbar .editor-toolbar__center ul.btns-box > li {
  padding: 0 5px;
}
.editor-container .editor-toolbar__top .editor-toolbar .editor-toolbar__right {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 30px;
}
.editor-container .editor-toolbar__top .editor-toolbar .cut-line {
  display: inline-block;
  border-right: 1px solid #5c5e5f;
  height: 40px;
  margin: 10px 8px;
}
.editor-container .editor-toolbar__top .editor-toolbar .btns-box {
  display: flex;
}
.editor-container .editor-toolbar__top .editor-toolbar .btns-box li {
  position: relative;
}
.editor-container .editor-toolbar__top .editor-toolbar .btns-box li:hover .btn-item__list {
  display: block;
}
.editor-container .editor-toolbar__top .editor-toolbar .btns-box li:hover .btn-container {
  background: #000;
  border-radius: 3px;
}
.editor-container .editor-toolbar__top .editor-toolbar .btns-box li .btn-container {
  text-align: center;
  padding: 4px 12px;
}
.editor-container .editor-toolbar__top .editor-toolbar .btns-box li .btn-container i {
  font-size: 18px;
  color: #FFF;
}
.editor-container .editor-toolbar__top .editor-toolbar .btns-box li .btn-container span {
  display: block;
  color: rgba(255, 255, 255, 0.75);
}
.editor-container .editor-toolbar__top .editor-toolbar .btns-box li .btn-container:hover {
  background: #000;
  border-radius: 3px;
  cursor: pointer;
}
.editor-container .editor-toolbar__top .editor-toolbar .btns-box li .btn-item__list {
  display: none;
  position: absolute;
  width: 460px;
  left: 50%;
  transform: translateX(-50%);
}
.editor-container .editor-toolbar__top .editor-toolbar .btns-box li .btn-item__list .list-container {
  margin-top: 10px;
  padding: 10px;
  border-radius: 3px;
  background: #FFF;
  max-height: 270px;
  overflow: auto;
}
.editor-container .editor-toolbar__top .editor-toolbar .btns-box li .btn-item__list .list-container ul {
  display: flex;
  flex-wrap: wrap;
}
.editor-container .editor-toolbar__top .editor-toolbar .btns-box li .btn-item__list .list-container ul li {
  margin-right: 8px;
  margin-bottom: 8px;
}
.editor-container .editor-toolbar__top .editor-toolbar .btns-box li .btn-item__list .list-container ul li:nth-child(5n) {
  margin-right: 0;
}
.editor-container .editor-toolbar__top .editor-toolbar .btns-box li .btn-item__list .list-container .tool-item {
  text-align: center;
  width: 79px;
  padding: 8px 0;
  cursor: pointer;
}
.editor-container .editor-toolbar__top .editor-toolbar .btns-box li .btn-item__list .list-container .tool-item .tool-item__icon {
  width: 44px;
  height: 44px;
  background: #F00;
  margin: 0 auto;
}
.editor-container .editor-toolbar__top .editor-toolbar .btns-box li .btn-item__list .list-container .tool-item:hover {
  background: #efefef;
}
.editor-container .editor-ctrl__left {
  position: absolute;
  width: 150px;
  top: 60px;
  bottom: 0;
  left: 0;
  background: #FFF;
}
.editor-container .editor-ctrl__info {
  position: absolute;
  top: 60px;
  left: 150px;
  right: 270px;
  height: 60px;
  background: #FFF;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 30px;
}
.editor-container .editor-ctrl__info .title {
  height: 40px;
  background: #1890ff;
  color: #FFF;
  border-radius: 3px;
  padding: 0 20px;
  font-size: 18px;
  line-height: 40px;
  cursor: pointer;
}
.editor-container .editor-ctrl__body {
  position: absolute;
  top: 120px;
  left: 150px;
  right: 270px;
  bottom: 70px;
  background: #efefef;
  padding: 30px;
  overflow: auto;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1) inset;
}
.editor-container .editor-ctrl__right {
  position: absolute;
  width: 270px;
  top: 60px;
  bottom: 0;
  right: 0;
  background: #FFF;
}
.editor-container .editor-ctrl__bottom {
  position: absolute;
  bottom: 0;
  left: 150px;
  right: 270px;
  height: 70px;
  background: #FFF;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.editor-container .editor-ctrl__bottom .zoom-tips {
  font-size: 14px;
}
.editor-container .editor-ctrl__bottom .zoom-slider {
  margin-left: 10px;
  margin-right: 20px;
  width: 240px;
}
.editor-container .editor-ctrl__bottom .ant-slider-mark-text {
  font-size: 12px;
}
.dashboard-container {
  position: relative;
  background-color: #333;
  user-select: none;
  width: 100%;
  height: 100%;
  transform-origin: 0 0;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
  transition: all 0.3s linear;
  transform: scale(1.6);
}
.dashboard-container .dashboard-grid-overlay {
  background-size: 10px 10px, 10px 10px;
  background-image: linear-gradient(rgba(255, 255, 255, 0.1) 1px, transparent 0px), linear-gradient(90deg, rgba(255, 255, 255, 0.1) 1px, transparent 0px);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  pointer-events: none;
}
#root {
  height: 100%;
}
.top-bar {
  background: #fff;
  display: flex;
  justify-content: space-between;
  padding: 0 16px;
  align-items: center;
}
